import React, { useCallback, useState } from 'react';
import { navigate } from 'gatsby';
import Page from 'components/Layout/Page';
import { Container, Grid, Typography, Paper, makeStyles, Theme, createStyles, Snackbar } from '@material-ui/core';
import Emoji from 'components/Utils/Emoji';
import MentoringApplyForm from 'components/Forms/MentoringApply';
import { MentoringApplyFormDto } from 'src/models/forms';
import { applyMentoringForm } from 'src/utils/api';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    paper: {
      padding: theme.spacing(2),
      [theme.breakpoints.up('md')]: {
        padding: theme.spacing(4),
      },
    },
  })
);

const MentoringApply = () => {
  const styles = useStyles();
  const [openErrorSnackbar, setOpenErrorSnackbar] = useState(false);

  const handleSubmit = useCallback(async (data: MentoringApplyFormDto) => {
    try {
      await applyMentoringForm(data);
      navigate('/mentoring/apply-result');
    } catch (e) {
      setOpenErrorSnackbar(true);
    }
  }, []);

  const handleSnackbarClose = useCallback(() => setOpenErrorSnackbar(false), []);

  return (
    <Page
      pageName="루비콘 멘토링 프로젝트 신청하기"
      showTitle={true}
      description={
        <Typography component="span">
          루비콘 멘토링 프로젝트에 오신 것을 환영합니다! <Emoji emoji="🙌" name="Hello" />
        </Typography>
      }
    >
      <Container>
        <Grid container justify="center">
          <Grid item xs={12} md={8}>
            <Paper className={styles.paper} elevation={3}>
              <MentoringApplyForm onSubmit={handleSubmit} />
            </Paper>
          </Grid>
        </Grid>
      </Container>
      <Snackbar
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'center',
        }}
        open={openErrorSnackbar}
        autoHideDuration={5000}
        onClose={handleSnackbarClose}
        message={`멘토링 프로젝트 신청에 약간의 문제가 있어요!😢 문제가 계속 된다면 lubycon@gmail.com으로 알려주세요`}
      />
    </Page>
  );
};

export default MentoringApply;
