import React, { ReactNode } from 'react';
import { makeStyles, FormControl, FormLabel, Theme, createStyles } from '@material-ui/core';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    fieldset: {
      marginBottom: theme.spacing(3),
    },
    formLabel: {
      marginBottom: theme.spacing(1),
    },
  })
);

interface Props {
  label: string;
  required?: boolean;
  children: ReactNode;
  error?: boolean;
}
const TextFieldset = ({ label, required = false, children, error = false }: Props) => {
  const styles = useStyles();

  return (
    <FormControl component="fieldset" required={required} fullWidth={true} error={error} className={styles.fieldset}>
      <FormLabel className={styles.formLabel}>{label}</FormLabel>
      {children}
    </FormControl>
  );
};

export default TextFieldset;
