import React, { useCallback, useMemo } from 'react';
import {
  Grid,
  FormGroup,
  FormControlLabel,
  Checkbox,
  TextField,
  Radio,
  Button,
  FormHelperText,
  RadioGroup,
  makeStyles,
} from '@material-ui/core';
import { useForm } from 'react-hook-form';
import Section from 'components/Forms/MentoringApply/Section';
import Fieldset from 'components/Forms/MentoringApply/Fieldset';
import { MentoringApplyFormDto } from 'models/forms';
import {
  jobs,
  positions,
  careers,
  applyReasons,
  workingTimes,
  workingLocations,
  ApplyReason,
  WorkingLocation,
} from 'src/constants/applyForm';

const useStyles = makeStyles(theme => ({
  root: {
    '& .MuiFormLabel-asterisk': {
      fontWeight: 'bold',
      color: theme.palette.primary.main,
    },
  },
  options: {
    borderRadius: theme.shape.borderRadius,
    marginRight: 7,
    paddingRight: 9,
    '&:hover': {
      backgroundColor: theme.palette.action.hover,
    },
  },
}));

interface Props {
  onSubmit?: (data: MentoringApplyFormDto) => void;
}
const MentoringApplyForm = ({ onSubmit }: Props) => {
  const styles = useStyles();
  const { register, handleSubmit, errors, watch } = useForm();
  const formValues = watch();

  const hasPosition = useMemo(() => {
    return formValues.positions && formValues.positions.length > 0;
  }, [formValues.positions]);

  const checkedExtraReason = useMemo(() => {
    return formValues.applyReasons && formValues.applyReasons.includes('기타');
  }, [formValues.applyReasons]);

  const checkedExtraLocation = useMemo(() => {
    return formValues.workingLocations && formValues.workingLocations.includes('기타');
  }, [formValues.workingLocations]);

  const onSubmitHandler = useCallback(
    data => {
      const formData = { ...data };
      const { job, otherJob, applyReasons, otherReason, workingLocations, otherLocation } = formData;
      if (job === '기타') {
        formData.job = otherJob;
        delete formData.otherJob;
      }
      if (checkedExtraReason) {
        const index = applyReasons.findIndex((reason: ApplyReason) => reason === '기타');
        formData.applyReasons[index] = otherReason;
        delete formData.otherReason;
      }

      if (checkedExtraLocation) {
        const index = workingLocations.findIndex((location: WorkingLocation) => location === '기타');
        formData.workingLocations[index] = otherLocation;
        delete formData.otherLocation;
      }

      onSubmit?.(formData);
    },
    [checkedExtraLocation, checkedExtraReason, onSubmit]
  );

  return (
    <Grid
      component="form"
      container
      noValidate
      autoComplete="off"
      onSubmit={(e: any) => e.preventDefault()}
      className={styles.root}
    >
      <Section title="연락에 필요한 기본정보를 작성해주세요.">
        <Fieldset label="이메일" required={true} error={Boolean(errors.email)}>
          <TextField
            name="email"
            type="email"
            placeholder="lubycon@gmail.com"
            variant="outlined"
            inputRef={register({
              required: {
                value: true,
                message: '이메일을 입력해주세요',
              },
              pattern: {
                value: /\b[\w\.-]+@[\w\.-]+\.\w{2,4}\b/,
                message: '올바른 이메일을 입력해주세요',
              },
            })}
            error={Boolean(errors.email)}
            helperText={errors.email?.message ?? ''}
          />
        </Fieldset>
        <Fieldset label="이름" required={true} error={Boolean(errors.name)}>
          <TextField
            name="name"
            placeholder="홍길동"
            variant="outlined"
            inputRef={register({
              required: {
                value: true,
                message: '이름을 입력해주세요',
              },
              pattern: {
                value: /[a-zA-Z가-힣]+/,
                message: '올바른 이름을 입력해주세요',
              },
            })}
            error={Boolean(errors.name)}
            helperText={errors.name?.message ?? ''}
          />
        </Fieldset>
        <Fieldset label="휴대폰 번호 (-는 빼주세요)" required={true} error={Boolean(errors.phoneNumber)}>
          <TextField
            name="phoneNumber"
            type="tel"
            placeholder="01012341234"
            variant="outlined"
            inputRef={register({
              required: {
                value: true,
                message: '휴대폰 번호를 입력해주세요',
              },
              pattern: {
                value: /^01\d\d{4}\d{4}$/,
                message: '올바른 휴대폰 번호를 입력해주세요',
              },
            })}
            error={Boolean(errors.phoneNumber)}
            helperText={errors.phoneNumber?.message}
          />
        </Fieldset>
      </Section>
      <Section title="현재 무슨 일을 하고 계시나요?">
        <Fieldset label="직업" required={true} error={Boolean(errors.job)}>
          <RadioGroup row defaultValue={jobs[0]}>
            {jobs.map(job => (
              <FormControlLabel
                key={job}
                name="job"
                label={job}
                value={job}
                control={<Radio />}
                inputRef={register({
                  required: {
                    value: true,
                    message: '직업을 선택해주세요',
                  },
                })}
                className={styles.options}
              />
            ))}
          </RadioGroup>
          {formValues.job === '기타' && (
            <TextField
              name="otherJob"
              placeholder="직업을 적어주세요"
              variant="outlined"
              inputRef={register({
                required: {
                  value: true,
                  message: '직업을 입력해주세요',
                },
              })}
              error={Boolean(errors.otherJob)}
              helperText={errors.otherJob?.message ?? ''}
            />
          )}
          {errors.job?.message && <FormHelperText>{errors.job?.message ?? ''}</FormHelperText>}
        </Fieldset>
        <Fieldset label="현재의 포지션 (복수 선택 가능)">
          <FormGroup row>
            {positions.map(position => (
              <FormControlLabel
                name="positions"
                key={position}
                label={position}
                value={position}
                inputRef={register()}
                control={<Checkbox />}
                className={styles.options}
              />
            ))}
          </FormGroup>
        </Fieldset>
        {hasPosition && (
          <Fieldset label="이 포지션의 경력이 어떻게 되시나요?" error={Boolean(errors.career)}>
            <RadioGroup row defaultValue={careers[0]}>
              {careers.map(carrer => (
                <FormControlLabel
                  key={carrer}
                  name="career"
                  label={carrer}
                  value={carrer}
                  inputRef={register({
                    required: {
                      value: true,
                      message: '경력을 알려주세요!',
                    },
                  })}
                  control={<Radio />}
                  className={styles.options}
                />
              ))}
            </RadioGroup>
            {errors.career && <FormHelperText>{errors.career?.message ?? ''}</FormHelperText>}
          </Fieldset>
        )}
      </Section>
      <Section title="프로젝트에 대해서">
        <Fieldset label="사이드 프로젝트에 참여하시는 이유가 무엇인가요? (복수 선택 가능)">
          <FormGroup>
            {applyReasons.map(reason => (
              <FormControlLabel
                name="applyReasons"
                key={reason}
                label={reason}
                value={reason}
                inputRef={register()}
                control={<Checkbox />}
                className={styles.options}
              />
            ))}
          </FormGroup>
          {checkedExtraReason && (
            <TextField name="otherReason" placeholder="이유를 적어주세요" variant="outlined" inputRef={register()} />
          )}
        </Fieldset>
        <Fieldset
          label="만약 사이드 프로젝트를 진행한다면 1주일에 몇 시간 정도 기여하실 수 있으신가요?"
          required={true}
          error={Boolean(errors.workingTime)}
        >
          <RadioGroup row defaultValue={workingTimes[0]}>
            {workingTimes.map(time => (
              <FormControlLabel
                key={time}
                name="workingTime"
                label={time}
                value={time}
                control={<Radio />}
                inputRef={register({
                  required: {
                    value: true,
                    message: '시간을 선택해주세요',
                  },
                })}
                className={styles.options}
              />
            ))}
          </RadioGroup>
          {errors.workingTime?.message && <FormHelperText>{errors.workingTime?.message ?? ''}</FormHelperText>}
        </Fieldset>
        <Fieldset
          label="오프라인 미팅을 하게 된다면 어떤 곳을 선호하시나요? (복수 선택 가능)"
          required={true}
          error={Boolean(errors.workingLocations)}
        >
          {workingLocations.map(location => (
            <FormControlLabel
              key={location}
              name="workingLocations"
              label={location}
              value={location}
              control={<Checkbox />}
              inputRef={register({
                required: {
                  value: true,
                  message: '원하는 장소를 선택해주세요',
                },
              })}
              className={styles.options}
            />
          ))}
          {checkedExtraLocation && (
            <TextField
              name="otherLocation"
              placeholder="원하는 장소를 적어주세요"
              variant="outlined"
              inputRef={register({
                required: {
                  value: true,
                  message: '장소를 적어주세요',
                },
              })}
            />
          )}
          {errors.workingLocations && <FormHelperText>{errors.workingLocations?.message ?? ''}</FormHelperText>}
        </Fieldset>
        <Fieldset label="평소에 생각하고 계시던 사이드 프로젝트 아이템이 있나요? 만약 있다면, 어떤 아이템을 생각 중이신가요?">
          <TextField name="projectItem" multiline={true} variant="outlined" inputRef={register()} />
        </Fieldset>
        <Fieldset label="혹시 루비콘에게 바라는 멘토링의 방향이 있으시다면 자유롭게 적어주세요!">
          <TextField name="expectThing" multiline={true} variant="outlined" inputRef={register()} />
        </Fieldset>
      </Section>
      <Section>
        <Fieldset
          label="인재풀 확보에 동의해주신다면, 만약 멘토링 프로젝트에서 떨어지더라도 프로젝트 중간에 인원 충원이 필요할
            경우에 가장 먼저 연락을 드릴 수 있습니다!"
        >
          <FormControlLabel
            name="isAgreeToCollectTalentPool"
            label="동의합니다"
            inputRef={register()}
            control={<Checkbox />}
          />
        </Fieldset>
        <Button
          type="submit"
          color="primary"
          size="large"
          variant="contained"
          fullWidth={true}
          onClick={handleSubmit(onSubmitHandler)}
        >
          신청하기
        </Button>
      </Section>
    </Grid>
  );
};

export default MentoringApplyForm;
